import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./jc-bay-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class JcBayAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;

 
jcBayForm: FormGroup;
bayId = 0;  
ajaxData : JcBayData[];
jcBay= new JcBayData();
status;

msg = [];
bootstrapFromValues = [];
 public branches=[];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.bayId).subscribe(params=>{
			if(params.bayId != undefined){
				this.bayId=params.bayId; 
			}
		}); 
	console.log("bayId=="+this.bayId);
	if(this.bayId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
		this.populateDropDownData();
	}
	}
	
    ngOnInit() {
		this.jcBayForm = new FormGroup({			
			'bayName' : new FormControl('', [ Validators.required]),
			'bayBranchId' : new FormControl('', [ Validators.required]),
			'bayOpen' : new FormControl('', [ Validators.required]),
			'bayNotes' : new FormControl('', [ Validators.required]),
			'bayActive' : new FormControl('', [ Validators.required]),
			'bayEntryId' : new FormControl(''),
			'bayEntryDate' : new FormControl(''),
			'bayModifiedId' : new FormControl(''),
			'bayModifiedDate' : new FormControl(''),
		});
		
	}
	  
	   	get bayName(){ return this.jcBayForm.get('bayName')};
		get bayBranchId(){ return this.jcBayForm.get('bayBranchId')};
		get bayOpen(){ return this.jcBayForm.get('bayOpen')};
		get bayNotes(){ return this.jcBayForm.get('bayNotes')};
		get bayActive(){ return this.jcBayForm.get('bayActive')};
	    get bayEntryId(){ return this.jcBayForm.get('bayEntryId')};
	    get bayEntryDate(){ return this.jcBayForm.get('bayEntryDate')};
	    get bayModifiedId(){ return this.jcBayForm.get('bayModifiedId')};
	    get bayModifiedDate(){ return this.jcBayForm.get('bayModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<JcBayData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcbay-data ',
							 			JSON.stringify({ 'bayId' :this.bayId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.jcBay = data['populateData'][0];
																		console.log("bayModifiedId=="+this.jcBay.bayModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
 	populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.branches = data['populateBranch']
			  															});
			 	  
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/jc-bay-list']);      
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.jcBay[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.jcBayForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.bayId == 0){ 
				this.jcBay['requestType'] = 'add';
			}else{
				this.jcBay['requestType'] = 'update';
				this.jcBay['bayId'] = this.bayId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcbay',
									JSON.stringify(this.jcBay),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.jcBayForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.jcBay);
	  }
 
  onDelete(){
		 this.jcBay['requestType'] = 'delete';
		this.jcBay['bayId'] = this.bayId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcbay',
									JSON.stringify(this.jcBay),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.jcBayForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}
	
export class  JcBayData {
   bayId:string;
	bayBranchId:string;
	bayOpen:string="true";
	bayActive:string="true";
	bayNotes:string;
	bayRank:string;
	bayName: string;
	bayEntryId: string;
	bayEntryBy: string;
	bayEntryDate: string;
	bayModifiedId: string;
	bayModifiedBy: string;
	bayModifiedDate: string;
  
}


